import * as React from 'react'
import styled from 'styled-components'
import SectionTemplate from './templates/SectionTemplate'
import * as speakersJson from '../speakers.json'
import SpeakerCard, { SpeakerCardProps } from './SpeakerCard'
import Slider, { Settings as SliderSetting } from 'react-slick'

const CardRow = styled.div`
  padding: 0 30px;
`

const sliderSettings: SliderSetting = {
  dots: true,
  slidesToShow: 4,
  slidesToScroll: 4,
  infinite: false,
  responsive: [
    { breakpoint: 1200, settings: { slidesToShow: 3, slidesToScroll: 3 } },
    { breakpoint: 900, settings: { slidesToShow: 2, slidesToScroll: 2 } },
    { breakpoint: 600, settings: { slidesToShow: 1, slidesToScroll: 1 } }
  ]
}

const SpeakerSection: React.StatelessComponent<{}> = () => (
  <SectionTemplate title="" iconClass="fa fa-microphone fa-2x">
    <CardRow>
      <Slider {...sliderSettings}>
        {speakersJson.speakers.map((item: SpeakerCardProps) => (
          <SpeakerCard {...item} />
        ))}
      </Slider>
    </CardRow>
  </SectionTemplate>
)

export default SpeakerSection



