import * as React from 'react'
import styled from 'styled-components'
import SectionTemplate from './templates/SectionTemplate'

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
`

const Card = styled.div`
  min-height: 380px;
  text-align: center;
  margin: 10px;
  box-shadow: 0 0 16px 0 rgba(32, 32, 47, 0.16);
`

const Head = styled.div`
  background: #23233f;
  padding: 30px 80px;
`

const CardTitle = styled.div`
  font-size: 16px;
  color: white;
`

const CardPrice = styled.div`
  font-size: 45px;
  margin-top: 20px;
  color: white;
`

const PriceUnit = styled.span`
  font-size: 20px;
`

const Body = styled.div`
  box-sizing: border-box;
  padding: 30px 10px;
  height: 100%;
`

const ButtonRow = styled.div`
  margin-top: 10px;
`

const Button = styled.button`
  margin-top: 40px;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 300;
  color: white;
  padding: 17px 20px;
  background: #20202f;
  border: none;
  transition: all 0.3s ease-in-out;
  cursor: pointer;

  &:hover {
    background: white;
    color: black;
  }
`

const Meta = styled.span`
  color: #dddddd;
`

const PriceSection: React.StatelessComponent<{}> = () => (
  <SectionTemplate title="" iconClass="fa fa-ticket fa-2x">
    <Wrapper>
      <Card>
        <Head>
          <CardTitle></CardTitle>
          <CardPrice>
            11,000 <PriceUnit></PriceUnit>
          </CardPrice>
        </Head>
        <Body>
               <br />
          <Meta>( )</Meta>
          <ButtonRow>
            <a target="_blank" href="https://festa.io">
              <Button> </Button>
            </a>
          </ButtonRow>
        </Body>
      </Card>
      <Card>
        <Head>
          <CardTitle></CardTitle>
          <CardPrice>
            22,000 <PriceUnit></PriceUnit>
          </CardPrice>
        </Head>
        <Body>
            <br />
          <Meta>( )</Meta>
        </Body>
      </Card>
    </Wrapper>
  </SectionTemplate>
)

export default PriceSection



